.gallery {
    background-image: url(./dist/svg/bg.svg);
    background-color: #F0F2F5;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.gallery__cards{
    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
    -webkit-column-width: 33%;
    -moz-column-width: 33%;
    column-width: 33%;
    padding: 0 12px;
}

.gallery .pics {
    -webkit-transition: all 350ms ease;
    transition: all 350ms ease;
    cursor: pointer;
    margin-bottom:12px;
}

.pics > img {
    border-radius: 20px;
}

.gallery .pics:hover {
    filter: opacity(.8);
}

.galleryCard{
    box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
}


/* ///////////Model///////////// */
.model{
    width:100%;
    height:100vh;
    position: fixed;
    top:0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000000;
    transition: opacity .4s ease, visibility .4s ease, transform .5s ease-in-out;
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    overflow: hidden;
    z-index: 9999999;
}

.model.open {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}

.model.open > span{
    position: fixed;
    top: 10px;
    right: 10px;
    width: 2rem;
    height: 2rem;
    padding: 5px;
    background-color: rgb(0, 0, 0, 0.4);
    color: #ffffff;
    cursor: pointer;
}

.model img {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    display: block;
    line-height: 0;
    box-sizing: border-box;
    padding: 20px 0 20px;
    margin: 0 auto; 
}


@media (max-width: 991px){
    .gallery__cards{
        -webkit-column-count: 2;
        -moz-column-count: 2;
        column-count: 2;
    }
}

@media (max-width: 500px){
    .gallery__cards{
        -webkit-column-count: 1;
        -moz-column-count: 1;
        column-count: 1;
        -webkit-column-width: 100%;
        -moz-column-width: 100%;
        column-width: 100%;
    }
}
